/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
body {
  font-family: sans-serif;
  background-image: url('sw-bg.jpg');
  background-size: cover;
}
.App {
  text-align: center;
  max-width: 1180px;
  width: 100%;
}
.App .Header {
  color: #443e3e;
  text-shadow: 1px 1px 5px #fff;
  width: 100%;
  margin-bottom: 35px;
}
.App li {
  margin-bottom: 35px;
}
.card-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  width: 100%;
  margin: 25px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.card-container h2 {
  -ms-flex-item-align: center;
      align-self: center;
  font-weight: 500;
  font-size: 2rem;
}
.card-container .cc {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  border: solid 1px #763F24;
  height: 250px;
  width: 200px;
  padding: 8px;
  color: #462516;
  background: #CECBC6;
  opacity: 50%;
  -webkit-box-shadow: 8px 8px 2px 1px rgba(0, 0, 0, 0.4);
          box-shadow: 8px 8px 2px 1px rgba(0, 0, 0, 0.4);
  margin: 24px 15px;
  border-radius: 5px;
}
.card-container .cc:hover {
  background: white;
}
h1 {
  -ms-flex-item-align: center;
      align-self: center;
  font-weight: 700;
  font-size: 3rem;
}
a {
  color: lightcyan;
  font-size: 1.5rem;
  font-weight: 500;
  padding: 8px;
  margin: 30px;
  background: darkgray;
  border-radius: 3px;
}



